import React from 'react';
import Countdown from 'react-countdown-now';

class Timer extends React.Component {
  state = {
    eventDate: new Date('11/9/19 9:00 AM EST')
  };

  render() {
    return (
      <Countdown date={new Date('11/9/19 9:00 AM EST')} renderer={renderer} />
    );
  }
}

const renderer = ({ days, hours, minutes, seconds, completed }) => {
  if (!completed) {
    return (
      <div className="countdown">
        <h1 className="countdown-title">Event Countdown</h1>
        <div className="countdown-timer">
          <div className="countdown-timer-content">
            <h3>Days</h3>
            <h4>{days}</h4>
          </div>
          <div className="countdown-timer-content">
            <h3>Hours</h3>
            <h4>{hours}</h4>
          </div>
          <div className="countdown-timer-content">
            <h3>Minutes</h3>
            <h4>{minutes}</h4>
          </div>
          <div className="countdown-timer-content">
            <h3>Seconds</h3>
            <h4>{seconds}</h4>
          </div>
        </div>
      </div>
    );
  }
};

export default Timer;



